import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Store } from '@ngrx/store';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { editarDecano, paginacionDecano } from 'src/app/store/actions/decano.actions';
import { AppState } from 'src/app/store/app.reducer';
import { SearchPagination } from '../../interfaces/search.pagination';
import { DecanoModel } from '../../models/gestion-academica/decano.model';
import { DecanoService } from '../../services/decano.service';
import { UtilAlert } from '../../util/util.alert';
import { UtilValidation } from '../../util/util.validation';

@Component({
  selector: 'app-modal-decano',
  templateUrl: './modal-decano.component.html',
  styles: [
  ]
})
export class ModalDecanoComponent implements OnInit {

  @BlockUI() blockUI: NgBlockUI;

  @ViewChild('modalDecano', { static: false })
  modalDecano: ElementRef;

  private _subscriptionDecano: Subscription;
  public decano: DecanoModel;
  public formDecano: FormGroup;

  public buscador: String = ""
  public page: number = 1;
  public pageSize: number = 10;
  public searchPagination: SearchPagination<string>;

  constructor(
    private modalParaDecano: NgbModal,
    private store: Store<AppState>,
    private utilValidation: UtilValidation,
    public alertService: UtilAlert,
    private toastr: ToastrService,
    private decanoService: DecanoService,
  ) { }

  ngOnInit(): void {
    this._decanoState();
    this.newFormDecano()
  }

  newFormDecano() {
    this.formDecano = new FormGroup({
      nombre: new FormControl("", [Validators.required]),
      facultad: new FormControl(""),
      correo: new FormControl("", [Validators.required, this.utilValidation.emailValido]),

    });
    this.formDecano.get("facultad").disable();
  }

  _decanoState() {
    this._subscriptionDecano = this.store
      .select("decano")
      .subscribe((state) => {
        if (state.loadedGuardado && state.decano) {
          this.alertService.successSwal(
            `Se ha editado correctamente el Decano`
          );

          this.obtenerPagination(state.searchPagination.seek.toString(), state.searchPagination.page, state.searchPagination.records);
          this.cerrarModal();
          this.listarDecanos();
          this.blockUI.stop();
        }
      });
  }
  openModal(decano: DecanoModel) {
    this.modalParaDecano.open(this.modalDecano, { size: 'sm', ariaLabelledBy: 'modal-basic-title', centered: true }).result.then(() => this.cerrarModal(false), () => this.cerrarModal(false));
    this.decano = decano;
    this.formDecano.get("nombre").setValue(this.decano.nombre);
    this.formDecano.get("correo").setValue(this.decano.correo);
    this.formDecano.get("facultad").setValue(this.decano.facultad.nombre);
    this.formDecano.get("facultad").disable();
  }

  cerrarModal(dimissAll = true) {
    if (dimissAll) this.modalParaDecano.dismissAll();
  }

  guardarCorreo() {
    UtilValidation.setFormForValidate(this.formDecano);
    if (this.formDecano.invalid) {
      this.toastr.info("Debe ingresar correctamente todos los datos antes de guardar");
      return;
    }
    this.blockUI.start("Guardando...")
    let decano = new DecanoModel()
    decano.nombre = this.formDecano.get("nombre").value;
    decano.correo = this.formDecano.get("correo").value;
    decano.facultad = this.decano.facultad;
    decano.id = this.decano.id
    this.store.dispatch(editarDecano({ decano: decano }));
  }

  obtenerPagination(buscador: string, page: number, pageSize: number) {
    this.buscador = buscador;
    this.page = page;
    this.pageSize = pageSize;
    this.searchPagination = {
      seek: buscador,
      page: page,
      records: pageSize
    }
    console.table(this.searchPagination)

  }

  listarDecanos() {
    this.store.dispatch(paginacionDecano({ searchPagination: this.searchPagination }))
    return;
  }
}
