import { FacultadModel } from './facultad.model';

export class DecanoModel {

    public id: number = 0;
    public facultad: FacultadModel = null;
    public nombre: string = "";
    public correo: string = "";

    constructor(fields?: {
        id?: number,
        facultad?: FacultadModel,
        nombre?: string,
        correo?: string,
    }) {
        if (fields) { Object.assign(this, fields); }
    }
}